  function ScrollTop(props) {
    const { children, window } = props;
    // Note that you normally won't need to set the window ref as useScrollTrigger
    // will default to window.
    // This is only being set here because the demo is in an iframe.
    const trigger = useScrollTrigger({
      target: window ? window() : undefined,
      disableHysteresis: true,
      threshold: 100,
    });
  
    const handleClick = (event) => {
      const anchor = (event.target.ownerDocument || document).querySelector('#back-to-top-anchor');
  
      if (anchor) {
        anchor.scrollIntoView({ behavior: 'smooth', block: 'center' });
      }
    };
  
    return (
      <Zoom in={trigger}>
        <div onClick={handleClick} role="presentation" className={props.classes.upButton}>
          {children}
        </div>
      </Zoom>
    );
  }
  
  ScrollTop.propTypes = {
    children: PropTypes.element.isRequired,
    /**
     * Injected by the documentation to work in an iframe.
     * You won't need it on your project.
     */
    window: PropTypes.func,
  };
  function ListItemLink(props) {
    return <ListItem button component="a" {...props} />;
  }
  const Navbar =(props) =>{
  const [state, setState] = React.useState({
    left: false,
  });

  const toggleDrawer = (open) => (event) => {
    if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return;
    }

    setState({ ...state, left: open });
  };
  const onClickClose =()=>{
    setState({ ...state, left: false });
  }

  const list = (anchor) => (
    <div
      className={clsx(props.classes.list, {
        [props.classes.fullList]: anchor === 'top' || anchor === 'bottom',
      })}
      role="presentation"
      onClick={toggleDrawer(false)}
      onKeyDown={toggleDrawer(false)}
    >
          <IconButton onClick={toggleDrawer(false)} aria-label="clear" className={props.classes.closeBtn}>
          <ClearIcon fontSize="medium" />
        </IconButton>
      <List>
        {['Help','Sign Up','Login'].map((text, index) => (
          <ListItem button key={text}>
            <ListItemIcon>{index % 2 === 0 ? <InboxIcon /> : <MailIcon />}</ListItemIcon>
            <ListItemText primary={text} />
          </ListItem>
        ))}
      </List>
    </div>
  );

  return (
    <div className={props.classes.root}>
        {/* AppBar Section Started */}
      <AppBar position="static" className={props.classes.appBar} color="inherit">
        <Toolbar>
        
          <Typography variant="h6" className={props.classes.title}>
            AIR GATER
          </Typography>
          <Button className={props.classes.navitem} color="inherit">Help</Button>
          <Button className={props.classes.navitem} color="inherit">Sign Up</Button>
          <Button className={props.classes.navitem} color="inherit">Login</Button>
          <IconButton onClick={toggleDrawer( true)} edge="start" className={clsx(props.classes.menuButton,"btnToggle")} color="inherit" aria-label="menu">
            <MenuIcon />
          </IconButton>
        </Toolbar>
      </AppBar>
      <div>
      {['left'].map((anchor) => (
        <React.Fragment key={anchor}>
          <Drawer anchor={anchor} open={state[anchor]} onClose={toggleDrawer(anchor, false)}>
            {list(anchor)}
          </Drawer>
        </React.Fragment>
      ))}
    </div>
    <Toolbar id="back-to-top-anchor" />
    </div>
  );
}
const AutoCompleteSection =(props)=>{
  const [state, setState]=React.useState({
   
    isPickerOpen:false,
    selectedDate:"",
    checkInDate:"",
    checkOutDate:"",
 
  })
  const OpenPicker =()=>{
    setState({...state,isPickerOpen:!state.isPickerOpen})
        }
   const onChangeDate =(date)=>{
      var month = new Array();
  month[0] = "Jan";
  month[1] = "Feb";
  month[2] = "Mar";
  month[3] = "Apr";
  month[4] = "May";
  month[5] = "Jun";
  month[6] = "Jul";
  month[7] = "Aug";
  month[8] = "Sep";
  month[9] = "Oct";
  month[10] = "Nov";
  month[11] = "Dec";
    const checkIn = `${date[0].getDate()} ${month[date[0].getMonth()]}`
    const checkOut = `${date[1].getDate()} ${month[date[1].getMonth()]}`
    

      setState({...state,selectedDate:date,isPickerOpen:false,checkInDate:checkIn,checkOutDate:checkOut})
      
    }
    return(
         
    <Container>
    {/* Body Section Started */}
    <Grid container>
        <Grid item lg={10} className={props.classes.mxAuto}>
        <Box mt={6} fontWeight={700} color="#484848" fontSize={48} className="heading">Explore Your Dream Stay</Box>
    <Box color="#484848" mb={5} className="paragraph" fontSize={19}>Rent Your Convenient Stays From The Local Host Available In 190+ Countries.</Box>
    <Box p={2} position="relative" borderTop="1px solid #e4e4e4" borderLeft="1px solid #e4e4e4" borderRight="1px solid #e4e4e4">
    <Button onClick={OpenPicker} className={props.classes.btn} size="large" variant="outlined" color="inherit">{state.selectedDate === "" ? "Dates" : `${state.checkInDate} - ${state.checkOutDate}`}</Button>
    <Button className={props.classes.btn} size="large" variant="outlined" color="inherit">Entire place</Button>
    <Button className={props.classes.btn} size="large" variant="outlined" color="inherit">private room</Button>
    <Button className={props.classes.btn} size="large" variant="outlined" color="inherit">Shared room</Button>
      <DateRangePicker
        onChange={onChangeDate}
        className="datepickerhome"
        isOpen={state.isPickerOpen}
        value={state.selectedDate}
      />
    </Box>
    <AutoComplete />
        </Grid>
    </Grid>
   
</Container>
    );
}
