import axios from "axios";
import qs from "query-string";
export const userServices = {
hotelListing,
};

const URL_PREFIX = "http://droidor.com/sapatay/index.php/api/searchHotels";


async function hotelListing() {
  const options = {
    headers: { 
      "Content-Type": "application/x-www-form-urlencoded",
    }
  };

  const { data: response } = await axios.post(
    URL_PREFIX, 
    qs.stringify({
        destination:"SAL",
  arrivalDate:"2020-06-01",
  departureDate:"2020-06-02"
      }),
    options
  );
  
  return response;
}
